@import "smart-grid";
@import "vars";
@import "../css/fonts.css";
@import "map";

@include reset();

h1 {
	font-family: "RobotoBold", sans-serif;
	font-size: 45px;
	line-height: 55px; }

p {
	font-family: "RobotoRegular", sans-serif;
	font-size: 16px;
	line-height: 22px;
	color: $p_color; }

.preloader {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: $background_section;
	z-index: 2;
	transition: 1s all;
	opacity: 1;
	visibility: visible;

	.loader {
		width: 75px;
		height: 75px;
		border: 4px solid #fff;
		border-radius: 50%;
		border-top-color: $main_color;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		animation: 1s spin infinite linear; }

	&.done {
		opacity: 0;
		visibility: hidden; } }

@keyframes spin {
	from {
		transform: translate(-50%, -50%) rotate(0deg); }
	to {
		transform: translate(-50%, -50%) rotate(360deg); } }



.btn_yellow {
