.pop_up {
	display: none;
	min-width: 300px;
	min-height: 200px;
	padding: 50px;
	position: fixed;
	box-sizing: border-box;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: #fff;
	z-index: 1000;
	@include xxs-block {
		width: 270px; }

	.close_btn {
		position: absolute;
		width: 20px;
		height: 20px;
		top: -10px;
		right: -30px;
		background-color: transparent;
		cursor: pointer;
		@include xxs-block {
			right: -10px;
			top: -20px; }
		&::before {
			content: "";
			position: absolute;
			width: 20px;
			height: 3px;
			background-color: $p_color;
			transform: rotate(-45deg); }
		&::after {
			content: "";
			position: absolute;
			width: 20px;
			height: 3px;
			background-color: $p_color;
			transform: rotate(45deg); } }
	h5 {
		font-family: "RobotoMedium", sans-serif;
		font-size: 28px;
		color: $p_color;
		margin-bottom: 30px; }

	.btn_yellow {
		width: 100%; }

	.pop_up_label {
		font-family: "RobotoRegular", sans-serif;
		font-size: 16px;
		color: $p_color;
		margin-bottom: 20px;
		display: inline-block; }

	.input {
	    display: block;
	    width: 100%;
	    padding: 9px 16px;
	    margin-right: 13px;
	    margin-bottom: 20px;
	    border: 2px solid $light_grey;
	    font-family: 'RobotoRegular',sans-serif;
	    font-size: 16px;
	    line-height: 24px;
	    color: $p_color;
	    box-sizing: border-box; } }

.pop_up_form {
	width: 100%;
	box-sizing: border-box; }

.overlay {
	display: none;
	position: fixed;
	background-color: $p_color;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0.2; }

.active {
	display: block; }



