.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #fafafa;
  z-index: 2;
  transition: 1s all;
  opacity: 1;
  visibility: visible;
}

.preloader .loader {
  width: 75px;
  height: 75px;
  border: 4px solid #fff;
  border-radius: 50%;
  border-top-color: #f9d84e;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: 1s spin infinite linear;
}

.preloader.done {
  opacity: 0;
  visibility: hidden;
}

@keyframes spin {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

